import ReactLoading from 'react-loading';
import React, { Component } from 'react';
import App from './content/router';
const Load = '/manifest.json';

class AppLoad extends Component {
    state = {
        someData: [],
        isLoading: true
      }
    
      componentDidMount() {
        // eslint-disable-next-line no-undef
        setTimeout(() => { this.someFetch() }, 250);
      }
    
      someFetch = () => {
        // eslint-disable-next-line no-undef
        fetch(Load)
          .then(res => res.json())
          .then(res => this.setState({ 
            someData: res,
            isLoading: false })
        )
      }
      
    render() {
        return (
            // eslint-disable-next-line react/react-in-jsx-scope
            <div>
                {this.state.isLoading ? (
                    // eslint-disable-next-line react/react-in-jsx-scope
                    <div id="load">
                        {/* eslint-disable-next-line react/react-in-jsx-scope */}
                        <center><ReactLoading type={"spin"} color={"white"} /></center>
                    </div>
                ) : (
                        // eslint-disable-next-line react/react-in-jsx-scope
                        <App />
                    )}
            </div>
        );
    }
}
export default AppLoad;